import React, { useCallback } from 'react';
import NavItem from './NavItem';
import ProfileImage from './ProfileImage';
import Conditional from '../../hocs/Conditional';
import { useAuth } from '../../hooks';

const NavItems = () => {
  const { auth, removeAuth } = useAuth();
  const { seq, name, profileImageUrl } = auth || {};

  const handleLogoutClick = useCallback(
    (event) => {
      event.preventDefault();
      removeAuth();
      alert(' .');
    },
    [removeAuth]
  );

  return (
    <ul className="nav">
      <Conditional condition={!auth}>
        <NavItem to="/login"></NavItem>
        <NavItem to="/signup"></NavItem>
      </Conditional>
      <Conditional condition={auth}>
        <NavItem to={`/user/${seq}`}>
          <ProfileImage src={profileImageUrl} />
          {name}
        </NavItem>
        <NavItem to="/logout" onClick={handleLogoutClick}>
          
        </NavItem>
      </Conditional>
    </ul>
  );
};

export default React.memo(NavItems);
